  animation-direction: normal;
  animation-fill-mode: forwards;
  animation-play-state: running;
}

@keyframes animset {
  from {
    transform: translateX(100%);
  }
  to {
    transform: translateX(0);
  }
}

@keyframes animset2 {
  from {
    transform: translateX(-100%);
