  to {
    transform: translate(-50%, -50%) scale(1);
  }
}

.dot {
  position: absolute;
  width: 20px;
  height: 20px;
  background-color: red;
  width: 1px;
  height: 1px;
}

.dot__txt {
  font-weight: 500;
  font-size: 13px;
  line-height: 17px;
  padding: 8px 10px;
  background-color: #fff;
  border-radius: 2px;
  position: absolute;
  bottom: 100%;
  margin-bottom: 26px;
  left: 0;
  white-space: nowrap;
  transform: rotateY(90deg) translateX(-50%);
  transition: transform 0.3s ease-in-out, color 0s ease-in-out;
  transform-origin: left center;
  color: transparent;
}

.dot__txt:after {
  content: "";
  border-top: 4px solid #fff;
  border-left: 4px solid transparent;
  border-right: 4px solid transparent;
  position: absolute;
  left: 50%;
  margin-left: -4px;
  top: 100%;
}

.dot__point {
  position: absolute;
  top: -14px;
  left: -14px;
  width: 30px;
  height: 30px;
  background: #5A30F0;
  border-radius: 50%;
  box-shadow: 0 0 0 10px rgba(90, 48, 240, 0.2);
  transition: all 0.3s ease-in-out;
  cursor: pointer;
}

.dot__point:after {
  content: "";
  position: absolute;
  background: url(../img/plus.svg) center center no-repeat;
  width: 12px;
  height: 12px;
  top: 50%;
  left: 50%;
  margin-left: -6px;
  margin-top: -6px;
  transform: rotate(0);
  transition: all 0.3s ease-in-out;
}

.dot__point:hover {
  box-shadow: 0 0 0 0 rgba(90, 48, 240, 0.2);
}

.dot__point:hover:after {
  transform: rotate(45deg);
}

.dot__point:hover + .dot__txt {
  color: #636C81;
  transform: rotateY(0) translateX(-50%);
  transition: transform 0.3s ease-in-out, color 0s 0.3s ease-in-out;
}

.dots {
  opacity: 0;
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  pointer-events: none;
  transition: all 0.3s ease-in-out;
}

/* max width 1054 pixels */
@media only screen and (max-width: 1054px) {
  .section-choise__inner .dots {
    opacity: 1;
    pointer-events: auto;
  }
  .section-choise__inner .img-set-wrapp .dots {
    opacity: 0;
    transition: all 0.3s ease-in-out;
  }
  .section-choise__inner .img-set-wrapp.active .dots {
    opacity: 1;
    transition: all 0.3s 1.1s ease-in-out;
  }
}

/* end media max width 1054 pixels */
/* Min width 1055 pixels */
@media only screen and (min-width: 1055px) {
  .section-choise__inner.visible .dots {
    opacity: 1;
    pointer-events: auto;
  }
  .section-choise__inner.visible .img-set-wrapp .dots {
